.footer__info {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 60px;
  height: 150px;
  background-color: #6f1eaa;
}

.image {
  height: 100%;
  width: 100%;
}

.footer__content {
  background-color: #48156d;
  padding: 50px 205px 50px 340px;
}

.content-block {
  display: flex;
  gap: 100px;
}

.footer__content-links {
  display: flex;
  flex-direction: column;
}

.a {
  text-decoration: none;
  color: #ffffff;
  margin-bottom: 6px;
  font-family: "Roboto", sans-serif;
}

.footer__content-title {
  font-size: 12px;
  line-height: 14px;
  color: #c282f0;
  letter-spacing: 0.6px;
  margin-bottom: 15px;
  font-family: "Roboto", sans-serif;
}

.footer__content-email {
  font-size: 12px;
  line-height: 14px;
  color: #c282f0;
  letter-spacing: 0.6px;
  margin-bottom: 15px;
  margin-top: 15px;
  font-family: "Roboto", sans-serif;
}

.footer__content-fax {
  font-size: 12px;
  line-height: 14px;
  color: #c282f0;
  letter-spacing: 0.6px;
  margin-bottom: 15px;
  margin-top: 15px;
  font-family: "Roboto", sans-serif;
}

.footer__content-social {
  flex-wrap: wrap;
  width: 160px;
}

.footer__content-img {
  margin: 0 30px 15px 0;
}

.footer__content-block {
  display: flex;
  color: #ffffff;
  margin-top: 70px;
  gap: 20px;
}

.footer__content-location > p {
  width: 135px;
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
  margin-top: 15px;
  font-family: "Roboto", sans-serif;
}

@media screen and (min-width: 1280px) and (max-width: 1700px) {
  .content-block {
    gap: 80px;
  }
}

@media screen and (min-width: 640px) and (max-width: 1279px) {
  .footer__info {
    height: 200px;
    flex-wrap: wrap;
    padding: 50px;
    gap: 4px;
  }

  .footer__content {
    padding: 50px;
  }

  .content-block {
    flex-wrap: wrap;
    gap: 20px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 320px) and (max-width: 639px) {
  .footer__info {
    gap: 5px;
    flex-wrap: wrap;
    height: 300px;
  }

  .wrapper-image {
    width: 120px;
  }

  .footer__content {
    padding: 15px;
  }

  .content-block {
    flex-wrap: wrap;
    margin: 10px auto;
    gap: 15px;
  }

  .footer__content-block {
    margin-top: 40px;
    gap: 15px;
    flex-direction: column;
  }
}
